/* About Styles */

.about {
    transition: all .3s ease;
    width: 100%;
    height: 100vh;
    background: #252525;
    padding-top: 100px;
    padding-bottom: 100px;
    @media (max-width: 991px) and (max-height: 990px ) {
        height: auto; }

    position: absolute;
    left: 0;
    right: 0;
    .container {
        width: 80%;
        display: grid;
        align-items: center;
        grid-template-columns: 50% 50%;
        transform: translateY(100px);
        @media (max-width: 991px) {
            width: 100%; }
        @media (max-width: 767px) {
            display: block;
            text-align: center;
            transform: translateY(90px); }
        .about-us {
            width: 100%;
            h1 {
                color: #1AC77D;
                font-weight: 600;
                font-size: 50px;
                margin-bottom: 45px;
                @media (max-width: 991px) {
                    margin-left: 30px;
                    font-size: 35px; }
                @media (max-width: 479px) {
                    margin-left: 0; } }
            p {
                color: white;
                font-size: 20px;
                padding-bottom: 45px;
                max-width: 620px;
                @media (max-width: 1500px) {
                    max-width: 90%; }
                @media (max-width: 991px) {
                    margin-left: 30px;
                    font-size: 18px; }
                @media (max-width: 479px) {
                    margin: 0 auto; } } }
        .desc {
            border: 10px solid #1AC77D;
            max-width: 740px;
            height: 750px;
            padding-left: 30px;
            padding-right: 30px;
            position: relative;
            padding-top: 50px;
            @media (max-width: 1709px) {
                height: 800px; }
            @media (max-width: 1100px) {
                padding-top: 50px; }
            @media (max-width: 991px) {
                padding-top: 100px;
                padding-bottom: 100px;
                width: 90%; }
            @media (max-width: 767px) {
                margin: 0 auto;
                max-height: auto;
                height: 100%;
                padding-top: 25px; }
            .mottos {
                h3 {}
                font-size: 50px;
                color: #1AC77D;
                @media (max-width: 991px) {
                    font-size: 35px; }
                p {
                    margin-top: 30px;
                    font-size: 20px;
                    color: white;
                    @media (max-width: 1354px) {
                        font-size: 16px; }
                    @media (max-width: 991px) {
                        font-size: 14px; }
                    @media (max-width: 767px) {
                        font-size: 20px; } }
                hr {
                    margin-top: 23px;
                    margin-bottom: 23px;
                    background: #1AC77D;
                    border: none;
                    height: 2px; } } } } }
